'use client'

import { Card, CardContent } from '@/components/ui/card'

interface MetricsCardProps {
  icon: string
  title: string
  value: string | number
  subtitle: string
  color: 'primary' | 'secondary' | 'accent'
  trend?: {
    value: number
    isPositive: boolean
  }
}

export default function MetricsCard({
  icon,
  title,
  value,
  subtitle,
  color,
  trend,
}: MetricsCardProps) {
  const colorClasses = {
    primary: 'from-primary/20 to-primary/5 border-primary/20',
    secondary: 'from-secondary/20 to-secondary/5 border-secondary/20',
    accent: 'from-accent/20 to-accent/5 border-accent/20',
  }

  const textColors = {
    primary: 'text-primary',
    secondary: 'text-secondary',
    accent: 'text-accent',
  }

  return (
    <Card className={`bg-gradient-to-br ${colorClasses[color]} backdrop-blur border-l-4 border-l-${color}`}>
      <CardContent className="p-6">
        <div className="flex items-start justify-between">
          <div>
            <p className="text-xs font-medium text-muted-foreground uppercase tracking-wider mb-2">
              {title}
            </p>
            <h3 className={`text-3xl font-bold ${textColors[color]} mb-2`}>
              {value}
            </h3>
            <p className="text-xs text-muted-foreground">{subtitle}</p>
          </div>
          <span className="text-3xl opacity-60">{icon}</span>
        </div>
        {trend && (
          <div className={`mt-4 text-xs font-medium ${trend.isPositive ? 'text-secondary' : 'text-accent'}`}>
            {trend.isPositive ? '‚Üë' : '‚Üì'} {Math.abs(trend.value)}%
          </div>
        )}
      </CardContent>
    </Card>
  )
}
