'use client'

import { Canvas } from '@react-three/fiber'
import { PerspectiveCamera, OrbitControls, Stars, Html, Sphere } from '@react-three/drei'
import { useRef, useMemo } from 'react'
import * as THREE from 'three'
import OrbitPath from './OrbitPath'

interface AsteroidVisualizerProps {
  asteroid?: {
    id: string
    name: string
    diameter: number
    velocity: number
    distance: number
    hazardLevel: 'low' | 'medium' | 'high'
  }
}

function Asteroid({ asteroid }: { asteroid?: AsteroidVisualizerProps['asteroid'] }) {
  const meshRef = useRef<THREE.Mesh>(null)

  const hazardColors = {
    low: '#00FF00',
    medium: '#FFA500',
    high: '#FF0000',
  }

  const color = asteroid ? hazardColors[asteroid.hazardLevel] : '#00FFFF'
  const size = asteroid ? Math.max(0.5, asteroid.diameter / 100) : 2

  const texture = useMemo(() => {
    const canvas = document.createElement('canvas')
    canvas.width = 64
    canvas.height = 64
    const ctx = canvas.getContext('2d')!
    
    // Create a rocky texture
    ctx.fillStyle = '#888888'
    ctx.fillRect(0, 0, 64, 64)
    
    for (let i = 0; i < 200; i++) {
      ctx.fillStyle = `rgba(0,0,0,${Math.random() * 0.5})`
      ctx.beginPath()
      ctx.arc(
        Math.random() * 64,
        Math.random() * 64,
        Math.random() * 3,
        0,
        Math.PI * 2
      )
      ctx.fill()
    }

    const textureObj = new THREE.CanvasTexture(canvas)
    return textureObj
  }, [])

  return (
    <mesh ref={meshRef} castShadow>
      <icosahedronGeometry args={[size, 4]} />
      <meshPhongMaterial
        map={texture}
        emissive={color}
        emissiveIntensity={0.3}
        shininess={30}
      />
    </mesh>
  )
}

function Earth() {
  const meshRef = useRef<THREE.Mesh>(null)

  const earthTexture = useMemo(() => {
    const canvas = document.createElement('canvas')
    canvas.width = 512
    canvas.height = 512
    const ctx = canvas.getContext('2d')!

    // Create a simple Earth representation
    ctx.fillStyle = '#1e3a8a'
    ctx.fillRect(0, 0, 512, 512)

    // Add continents
    ctx.fillStyle = '#10b981'
    ctx.beginPath()
    ctx.arc(150, 200, 80, 0, Math.PI * 2)
    ctx.fill()

    ctx.beginPath()
    ctx.arc(350, 150, 60, 0, Math.PI * 2)
    ctx.fill()

    ctx.beginPath()
    ctx.arc(300, 350, 70, 0, Math.PI * 2)
    ctx.fill()

    return new THREE.CanvasTexture(canvas)
  }, [])

  return (
    <mesh ref={meshRef} castShadow>
      <sphereGeometry args={[1, 64, 64]} />
      <meshPhongMaterial map={earthTexture} shininess={5} />
    </mesh>
  )
}

function Scene({ asteroid }: AsteroidVisualizerProps) {
  return (
    <>
      <PerspectiveCamera makeDefault position={[0, 0, asteroid ? 8 : 5]} />
      <OrbitControls
        enableZoom={true}
        enablePan={true}
        autoRotate={true}
        autoRotateSpeed={2}
      />

      <Stars radius={100} depth={50} count={1000} factor={4} />

      {/* Lighting */}
      <ambientLight intensity={0.5} />
      <pointLight position={[10, 10, 10]} intensity={1} />
      <pointLight position={[-10, -10, -10]} intensity={0.3} />

      {/* Earth in center */}
      <group position={[0, 0, 0]}>
        <Earth />
        <mesh position={[0, 0, 0]}>
          <sphereGeometry args={[1.02, 64, 64]} />
          <meshBasicMaterial wireframe color="#00FFFF" transparent opacity={0.2} />
        </mesh>
      </group>

      {/* Asteroid */}
      {asteroid && (
        <group position={[3, 2, 4]}>
          <Asteroid asteroid={asteroid} />
          <mesh position={[0, 0, 0]}>
            <sphereGeometry args={[Math.max(0.5, asteroid.diameter / 100) + 0.3, 32, 32]} />
            <meshBasicMaterial
              wireframe
              color={
                asteroid.hazardLevel === 'high'
                  ? '#FF0000'
                  : asteroid.hazardLevel === 'medium'
                    ? '#FFA500'
                    : '#00FF00'
              }
              transparent
              opacity={0.3}
            />
          </mesh>
        </group>
      )}

      {/* Orbital paths */}
      <group>
        <OrbitPath radius={1.5} color="#00FFFF" opacity={0.15} />
        <OrbitPath radius={3} color="#00FF00" opacity={0.1} />
        <OrbitPath radius={5} color="#FFA500" opacity={0.08} />
        {asteroid && <OrbitPath radius={Math.sqrt(asteroid.distance / 1000000)} color="#FF0000" opacity={0.2} />}
      </group>

      {/* Info labels */}
      {asteroid && (
        <Html position={[3, 2, 4]} distanceFactor={1.5}>
          <div className="bg-black/80 border border-primary px-3 py-2 rounded-lg text-xs whitespace-nowrap pointer-events-none">
            <div className="text-primary font-semibold">{asteroid.name}</div>
            <div className="text-secondary text-[10px] mt-1">
              Distance: {asteroid.distance.toFixed(1)}M km
            </div>
            <div className="text-secondary text-[10px]">
              Velocity: {asteroid.velocity.toFixed(1)} km/s
            </div>
          </div>
        </Html>
      )}
    </>
  )
}

export default function AsteroidVisualizer({ asteroid }: AsteroidVisualizerProps) {
  return (
    <div className="w-full h-full rounded-lg overflow-hidden bg-gradient-to-br from-background to-card/50 border border-primary/20">
      <Canvas dpr={[1, 2]}>
        <Scene asteroid={asteroid} />
      </Canvas>
    </div>
  )
}
