'use client'

import { useState, useEffect } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import AsteroidVisualizer from './AsteroidVisualizer'
import AsteroidFeed from './AsteroidFeed'
import RiskAssessment from './RiskAssessment'
import MetricsCard from './MetricsCard'

interface Asteroid {
  id: string
  name: string
  diameter: number
  velocity: number
  distance: number
  estimatedDiameter: {
    kilometers: {
      estimated_diameter_min: number
      estimated_diameter_max: number
    }
  }
  close_approach_data: Array<{
    miss_distance: {
      kilometers: string
    }
    relative_velocity: {
      kilometers_per_second: string
    }
  }>
  is_potentially_hazardous_asteroid: boolean
}

export default function Dashboard() {
  const [asteroids, setAsteroids] = useState<Asteroid[]>([])
  const [selectedAsteroid, setSelectedAsteroid] = useState<Asteroid | null>(null)
  const [isLoading, setIsLoading] = useState(true)
  const [error, setError] = useState('')
  const [watched, setWatched] = useState<Set<string>>(new Set())

  useEffect(() => {
    const token = localStorage.getItem('token')
    if (!token) {
      window.location.href = '/login'
      return
    }

    fetchAsteroids()
  }, [])

  const fetchAsteroids = async () => {
    try {
      setIsLoading(true)
      const response = await fetch('http://localhost:8000/feed')
      if (!response.ok) throw new Error('Failed to fetch asteroids')
      
      const data = await response.json()
      setAsteroids(data.near_earth_objects || [])
      if (data.near_earth_objects && data.near_earth_objects.length > 0) {
        setSelectedAsteroid(data.near_earth_objects[0])
      }
    } catch (err) {
      setError('Failed to load asteroid data')
      console.error('[v0] Fetch error:', err)
    } finally {
      setIsLoading(false)
    }
  }

  const getHazardLevel = (asteroid: Asteroid): 'low' | 'medium' | 'high' => {
    if (!asteroid.close_approach_data || asteroid.close_approach_data.length === 0) {
      return 'low'
    }
    
    const distance = parseFloat(asteroid.close_approach_data[0].miss_distance.kilometers)
    const diameter = asteroid.estimatedDiameter?.kilometers?.estimated_diameter_max || 0

    if (asteroid.is_potentially_hazardous_asteroid && distance < 20000000) {
      return 'high'
    }
    if (distance < 50000000 && diameter > 100) {
      return 'medium'
    }
    return 'low'
  }

  const toggleWatchlist = async (asteroidId: string) => {
    const token = localStorage.getItem('token')
    if (!token) return

    try {
      if (watched.has(asteroidId)) {
        await fetch(`http://localhost:8000/alerts/unwatch/${asteroidId}`, {
          method: 'POST',
          headers: { 
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          },
        })
        setWatched(prev => {
          const newSet = new Set(prev)
          newSet.delete(asteroidId)
          return newSet
        })
      } else {
        await fetch('http://localhost:8000/alerts/watch', {
          method: 'POST',
          headers: { 
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          },
          body: JSON.stringify({ neoId: asteroidId }),
        })
        setWatched(prev => new Set(prev).add(asteroidId))
      }
    } catch (err) {
      console.error('[v0] Watchlist error:', err)
    }
  }

  const selectedProcessed = selectedAsteroid ? {
    id: selectedAsteroid.id,
    name: selectedAsteroid.name,
    diameter: selectedAsteroid.estimatedDiameter?.kilometers?.estimated_diameter_max || 50,
    velocity: selectedAsteroid.close_approach_data?.[0]?.relative_velocity?.kilometers_per_second 
      ? parseFloat(selectedAsteroid.close_approach_data[0].relative_velocity.kilometers_per_second)
      : 15,
    distance: selectedAsteroid.close_approach_data?.[0]?.miss_distance?.kilometers
      ? parseFloat(selectedAsteroid.close_approach_data[0].miss_distance.kilometers)
      : 1000000,
    hazardLevel: getHazardLevel(selectedAsteroid),
  } : undefined

  const hazardBadgeColor = {
    low: 'bg-secondary text-secondary-foreground',
    medium: 'bg-accent text-accent-foreground',
    high: 'bg-destructive text-destructive-foreground',
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-background via-background to-card/30 p-6 lg:p-8">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-4">
          <div>
            <h1 className="text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-primary via-secondary to-accent mb-2">
              AstroTrack Dashboard
            </h1>
            <p className="text-muted-foreground">Real-time NEO monitoring and impact risk assessment</p>
          </div>
          <Button 
            onClick={() => {
              localStorage.removeItem('token')
              localStorage.removeItem('userId')
              window.location.href = '/login'
            }}
            className="bg-destructive/20 hover:bg-destructive/30 text-destructive border border-destructive/30"
          >
            Logout
          </Button>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <MetricsCard
          icon="üåç"
          title="Total NEOs"
          value={asteroids.length}
          subtitle="Currently tracked"
          color="primary"
        />

        <MetricsCard
          icon="‚ö†Ô∏è"
          title="Hazardous"
          value={asteroids.filter(a => a.is_potentially_hazardous_asteroid).length}
          subtitle="Potentially hazardous"
          color="accent"
        />

        <MetricsCard
          icon="‚≠ê"
          title="Watchlist"
          value={watched.size}
          subtitle="Under surveillance"
          color="secondary"
        />
      </div>

      {/* Main Content */}
      <div className="grid grid-cols-1 lg:grid-cols-4 gap-6 mb-8">
        {/* 3D Visualizer */}
        <div className="lg:col-span-2">
          <Card className="border-primary/20 bg-card/40 backdrop-blur h-[500px] overflow-hidden">
            <CardHeader>
              <CardTitle className="text-lg">3D Asteroid View</CardTitle>
              <CardDescription>Interactive visualization of selected asteroid</CardDescription>
            </CardHeader>
            <CardContent className="h-[calc(100%-100px)] p-0">
              <AsteroidVisualizer asteroid={selectedProcessed} />
            </CardContent>
          </Card>
        </div>

        {/* Selected Asteroid Details */}
        <div className="lg:col-span-1">
          <Card className="border-primary/20 bg-card/60 backdrop-blur h-full">
            <CardHeader>
              <CardTitle className="text-lg">Object Details</CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              {selectedAsteroid ? (
                <>
                  <div>
                    <h3 className="font-semibold text-foreground mb-2 truncate">{selectedAsteroid.name}</h3>
                    <Badge className={hazardBadgeColor[getHazardLevel(selectedAsteroid)]}>
                      {getHazardLevel(selectedAsteroid).toUpperCase()}
                    </Badge>
                  </div>

                  <div className="space-y-3 text-sm">
                    <div>
                      <p className="text-muted-foreground text-xs">Diameter</p>
                      <p className="text-foreground font-medium text-sm">
                        {selectedAsteroid.estimatedDiameter?.kilometers?.estimated_diameter_max?.toFixed(2) || 'N/A'} km
                      </p>
                    </div>

                    <div>
                      <p className="text-muted-foreground text-xs">Distance</p>
                      <p className="text-foreground font-medium text-sm">
                        {selectedAsteroid.close_approach_data?.[0]?.miss_distance?.kilometers
                          ? `${(parseFloat(selectedAsteroid.close_approach_data[0].miss_distance.kilometers) / 1000000).toFixed(2)} M km`
                          : 'N/A'}
                      </p>
                    </div>

                    <div>
                      <p className="text-muted-foreground text-xs">Velocity</p>
                      <p className="text-foreground font-medium text-sm">
                        {selectedAsteroid.close_approach_data?.[0]?.relative_velocity?.kilometers_per_second
                          ? `${parseFloat(selectedAsteroid.close_approach_data[0].relative_velocity.kilometers_per_second).toFixed(2)} km/s`
                          : 'N/A'}
                      </p>
                    </div>
                  </div>

                  <Button
                    onClick={() => toggleWatchlist(selectedAsteroid.id)}
                    size="sm"
                    className={`w-full ${
                      watched.has(selectedAsteroid.id)
                        ? 'bg-secondary hover:bg-secondary/90'
                        : 'bg-primary/20 hover:bg-primary/30 text-primary border border-primary/30'
                    }`}
                  >
                    {watched.has(selectedAsteroid.id) ? '‚≠ê Watched' : '‚òÜ Watch'}
                  </Button>
                </>
              ) : (
                <p className="text-muted-foreground text-center py-8 text-xs">Select an asteroid</p>
              )}
            </CardContent>
          </Card>
        </div>

        {/* Risk Assessment */}
        <div className="lg:col-span-1">
          <RiskAssessment
            data={
              selectedAsteroid
                ? {
                    level: getHazardLevel(selectedAsteroid),
                    score: selectedAsteroid.is_potentially_hazardous_asteroid ? 75 : 30,
                    factors: [
                      {
                        name: 'Size',
                        severity: selectedAsteroid.estimatedDiameter?.kilometers?.estimated_diameter_max > 100 ? 'high' : 'low',
                        value: `${selectedAsteroid.estimatedDiameter?.kilometers?.estimated_diameter_max?.toFixed(1) || 0} km diameter`,
                      },
                      {
                        name: 'Distance',
                        severity: selectedAsteroid.close_approach_data?.[0]?.miss_distance?.kilometers
                          ? parseFloat(selectedAsteroid.close_approach_data[0].miss_distance.kilometers) < 20000000
                            ? 'high'
                            : 'low'
                          : 'low',
                        value: selectedAsteroid.close_approach_data?.[0]?.miss_distance?.kilometers
                          ? `${(parseFloat(selectedAsteroid.close_approach_data[0].miss_distance.kilometers) / 1000000).toFixed(2)} M km`
                          : 'Unknown',
                      },
                      {
                        name: 'Velocity',
                        severity: selectedAsteroid.close_approach_data?.[0]?.relative_velocity?.kilometers_per_second
                          ? parseFloat(selectedAsteroid.close_approach_data[0].relative_velocity.kilometers_per_second) > 20
                            ? 'high'
                            : 'medium'
                          : 'low',
                        value: selectedAsteroid.close_approach_data?.[0]?.relative_velocity?.kilometers_per_second
                          ? `${parseFloat(selectedAsteroid.close_approach_data[0].relative_velocity.kilometers_per_second).toFixed(2)} km/s`
                          : 'Unknown',
                      },
                    ],
                  }
                : null
            }
          />
        </div>
      </div>

      {/* Feed */}
      <div className="mt-8">
        <AsteroidFeed 
          asteroids={asteroids}
          selectedId={selectedAsteroid?.id}
          onSelect={setSelectedAsteroid}
          isLoading={isLoading}
          watched={watched}
          onWatchToggle={toggleWatchlist}
          getHazardLevel={getHazardLevel}
        />
      </div>

      {error && (
        <div className="fixed bottom-4 right-4 bg-destructive/10 border border-destructive/30 rounded-lg p-4 text-destructive">
          {error}
        </div>
      )}
    </div>
  )
}
